import Vue from 'vue'
import Vuex from 'vuex'
import Bus from '@/bus'
import Content from '@/main.js'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        // 登陆状态
        state: 0,
        selectPage: '',
        codeData: [],
        codeName: [],
        codeFolderId: [],
        shareState: 0,
        codeFileSave: {},
        codeFile: {},
        nodes: {},
        datas: {},

        appLet: {
            codeName: [],
            codeData: [],
            codeFolderId: [],
            codeFileSave: {},
            codeFile: {},
            nodes: {},
            datas: {}
        },

        // 日志
        logs: []
    },
    mutations: {
        // 单文件调试，小程序，共享区
        setSelectPage (state, info) {
            state.selectPage = info
        },
        // 新增标签页
        setcodeData (state, info) {
            if (state.codeFolderId.indexOf(info.folderId) === -1) {
                state.codeName.push(info.name)
                state.codeData.push(info)
                state.codeFolderId.push(info.folderId)
            }
        },
        // 关闭标签页
        removeCode (state, info) {
            let idx = state.codeFolderId.indexOf(info)
            state.codeName.splice(idx, 1)
            state.codeData.splice(idx, 1)
            state.codeFolderId.splice(idx, 1)
        },
        changeShareState (state, info) {
            state.shareState ? state.shareState = 0 : state.shareState = 1
        },
        // 原文件内容
        setCodeFileSave (state, info) {
            state.codeFileSave[info.folderId] = info.data
        },
        setCodeFileSave2 (state, info) {
            state.codeFileSave = state.codeFile
        },
        // 编辑文件内容
        setCodeFile (state, info) {
            state.codeFile[info.folderId] = info.data
        },
        // 应付重命名功能
        codeNameSplice (state, info) {
            state.codeName.splice(info, 1)
        },
        codeDataSplice (state, info) {
            state.codeData.splice(info, 1)
        },
        // 文件夹重命名 已废弃
        fileRename (state, info) {
            for (let i = 0; i < state.codeFolderId.length; i++) {
                if (state.codeFolderId[i].indexOf(info.old) !== -1) {
                    // let oldId = state.codeFolderId[i]
                    // let arr = state.codeFolderId[i].split(info.old)
                    // let newId = info.new + arr[1]
                    // state.codeFolderId[i] = newId
                    // state.codeData[i].folderId = newId
                    // state.codeFolderId.splice(i, 1)
                    // state.codeData.splice(i, 1)
                    // state.codeFile[newId] = state.codeFile[oldId]
                    // state.codeFileSave[newId] = state.codeFile[oldId]
                    // delete state.codeFile[oldId]
                    // delete state.codeFileSave[oldId]
                    // Bus.$emit('fileRename')
                }
            }
        },
        // 文件夹重命名
        inspectFile (state, info) {
            for (let i = 0; i < state.codeFolderId.length; i++) {
                if (state.codeFolderId[i].indexOf(info) !== -1) {
                    Content.$message({type: 'error', message: '该文件夹内正有文件被使用，请先关闭文件'})
                    return false
                }
            }
            Bus.$emit('fileRename')
        },
        codeFileSplice (state, info) {
            state.codeFile[info.newFoloderId] = state.codeFile[info.oldFolderId]
            state.codeFileSave[info.newFoloderId] = state.codeFile[info.oldFolderId]
            delete state.codeFile[info.oldFolderId]
            delete state.codeFileSave[info.oldFolderId]
        },
        // 当前小程序
        setSelectAppLet (state, info) {
            state.appLet.SelectAppLetId = info
        },
        // 小程序新增标签页
        setAppCodeData (state, info) {
            if (state.appLet.codeFolderId.indexOf(info.folderId) === -1) {
                state.appLet.codeName.push(info.name)
                state.appLet.codeData.push(info)
                state.appLet.codeFolderId.push(info.folderId)
            }
        },
        // 小程序关闭标签页
        removeAppCode (state, info) {
            let idx = state.appLet.codeFolderId.indexOf(info)
            state.appLet.codeName.splice(idx, 1)
            state.appLet.codeData.splice(idx, 1)
            state.appLet.codeFolderId.splice(idx, 1)
        },
        // 小程序原文件内容
        setAppCodeFileSave (state, info) {
            state.appLet.codeFileSave[info.folderId] = info.data
        },
        setAppCodeFileSave2 (state, info) {
            state.appLet.codeFileSave = state.codeFile
        },
        // 小程序编辑文件内容
        setAppCodeFile (state, info) {
            state.appLet.codeFile[info.folderId] = info.data
        },
        inspectFileApp (state, info) {
            for (let i = 0; i < state.appLet.codeFolderId.length; i++) {
                if (state.appLet.codeFolderId[i].indexOf(info) !== -1) {
                    Content.$message({type: 'error', message: '该文件夹内正有文件被使用，请先关闭文件'})
                    return false
                }
            }
            Bus.$emit('fileRenameApp')
        },
        // 小程序应付重命名功能
        appCodeNameSplice (state, info) {
            state.codeName.splice(info, 1)
        },
        appCodeDataSplice (state, info) {
            state.codeData.splice(info, 1)
        },
        appCodeFileSplice (state, info) {
            state.codeFile[info.newFoloderId] = state.codeFile[info.oldFolderId]
            state.codeFileSave[info.newFoloderId] = state.codeFile[info.oldFolderId]
            delete state.codeFile[info.oldFolderId]
            delete state.codeFileSave[info.oldFolderId]
        },
        // 获取日志
        setLogs (state, info) {
            state.logs.push(info)
        },
        emptyLogs (state) {
            state.logs.splice(0, state.logs.length)
        },
        // 单文件调试的树数据
        setNodes (state, info) {
            state.nodes = info.node
            state.datas = info.data
        },
        setAppNodes (state, info) {
            // console.log(info)
            state.appLet.nodes = info.node
            state.appLet.datas = info.data
        },
        // 改变登录状态
        setState (state, info) {
            state.state = info
        }
    }
})



